/*  =========================================================================
	Pattern-scaffolding
	This stylesheet is for styles you want to include only when displaying demo
	styles for animations, color swatches, etc.
	These styles will not be your production CSS.
	========================================================================= */

/*  Module
	========================================================================= */

@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,400;0,700;1,400;1,700&family=Raleway:ital,wght@0,300;0,400;0,600;1,300;1,400;1,600&display=swap');

#sg-patterns {
	box-sizing: border-box !important;
	max-width: 100%;
	padding: 0 0.5em;
}

.demo-animate {
	background: #dddddd;
	padding: 1em;
	margin-bottom: 1em;
	text-align: center;
	border-radius: 8px;
	cursor: pointer;
}

.sg-colors {
	display: flex;
	flex-wrap: wrap;
	list-style: none !important;
	padding: 0 !important;
}

.sg-colors li {
	flex: auto;
	padding: 0.3em;
	margin: 0 0.5em 0.5em 0;
	min-width: 5em;
	max-width: 14em;
	border: 1px solid #dddddd;
	border-radius: 8px;
	background-color: #ffffff;
}

.sg-swatch {
	display: block;
	height: 4em;
	margin-bottom: 0.3em;
	border-radius: 5px;
	border: 1px solid #dddddd;
}

.sg-label {
	font-size: 90%;
	line-height: 1;
}

.sg-text {
	font-family: 'Merriweather', serif;

	h1, h2, h3, h4, h5, h6 {
		font-family: 'Raleway', sans-serif;
		font-weight: 600;
	}
}

// Pattern Lab UI
.pl-c-header,
.pl-c-nav__list,
.pl-c-nav__link,
.pl-c-button,
.pl-c-typeahead__input,
.pl-c-viewport-size__input,
.pl-c-category__title,
.pl-c-pattern__title,
.pl-c-pattern__title-link,
.pl-c-pattern__extra-toggle,
.pl-c-drawer,
.pl-c-tabs,
.pl-c-code-copy-btn {
	font-family: 'Raleway', sans-serif !important;
}

.pl-c-category__title a {
	text-decoration: none;
}

.pl-c-text-passage {
	font-family: 'Merriweather', serif;
	font-size: 0.875rem;
	line-height: 1.5rem;
}
